img {
  width: 200px;
  height: 200px;
}
.componentWrapper {
  display: flex;
  flex-flow: column wrap;
  margin: 0;
  align-items: center;
  justify-content: center;
}
.component {
  --text-shadow: 0.25rem 0.25rem 1rem rgb(0, 0, 0, 0.15);
  --cover-shadow: 0rem 0.35rem 1.1rem rgb(0, 0, 0, 0.25);
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  padding: 2rem 3rem;
  margin: 0.7rem 0rem;
  background-color: whitesmoke;
  box-shadow: 0.1rem 1rem 2rem rgb(0, 0, 0, 0.08),
    0.1rem 1rem 2.6rem rgb(0, 0, 0, 0.07);
  border-radius: 1rem;
  width: 100%;
}
.cover {
  box-shadow: var(--cover-shadow);
  width: 100%;
}

/* Stacks */
.audioStack,
.cartStack,
.coverStack {
  display: inline-flex;
  flex-direction: column;
  align-self: flex-start;
  padding: 1rem;
  min-width: fit-content;
}
.cartStack {
  display: inline-flex;
  flex-direction: column;
  padding-top: 2.25rem;
}
/* Stack elements */
.audioStackElem,
.cartStackElem,
.selectButtonElem,
.inCartElem {
  padding: 0.5rem;
  margin-left: 1rem;
  margin-right: 1rem;
}
.cartStackElem,
.inCartElem {
  display: inline-flex;
  flex-direction: row;
  align-items: center;
  justify-self: center;
}
/* TODO: stylize in cart description */
.inCartElem {
  flex-direction: column;
  --bg-color-alpha: rgb(105, 216, 45, 0.5);
  --bg-color: rgb(105, 216, 45);
  /* padding: 2rem 2rem 0.9rem 2rem; */
  background-color: var(--bg-color-alpha);
  align-items: center;
  text-justify: distribute;
  border-radius: 1rem;
  color: rgb(56, 56, 56);
  box-shadow: 0rem 0rem 0.2rem 0rem var(--bg-color);
}

.details {
  box-shadow: 1.2rem 0.5rem 3rem 0 rgba(0, 0, 0, 0.04),
    -1rem 1rem 3rem 0.1rem rgba(0, 0, 0, 0.03);
}
/* TODO: color in add, rm buttons and style better  */
.addToCartButton,
.rmFromCartButton {
  width: 100%;
  margin: 0.5rem;
  padding: 0.25rem;
  border-radius: 100rem;
}
.addToCartButton:hover {
  box-shadow: 0rem 0rem 0.15rem var(--black-hover);
}
.rmFromCartButton:hover {
  box-shadow: 0rem 0rem 0.15rem var(--danger-hover);
}
.title {
  --offset: 0.95rem;
  --offset-opp: -0.95rem;
  display: inline-flex;
  flex-direction: row;
  font-variation-settings: "wght" 700;
  font-style: italic;
  padding-top: 1rem;
  padding-bottom: 1rem;
  margin-left: var(--offset-opp);
  text-shadow: var(--text-shadow);
}
.titleText {
  display: inline-flex;
  max-height: min-content;
  width: fit-content;
  background-color: rgb(255, 255, 0, 0.85);
  box-shadow: 0.2rem 0rem 0.5rem 0rem rgb(255, 255, 0, 1),
    -0.2rem 0rem 0.5rem 0rem rgb(255, 255, 0, 1);
  border-radius: 0.25rem;
  padding: 0rem var(--offset);
}
.showDetails {
  padding-left: 1rem;
  margin-bottom: 0.35rem;
  display: inline-flex;
  font-size: 0.8rem;
  opacity: 0.6;
  max-width: fit-content;
  /* margin-bottom: -0rem; */
}
.showDetails:hover {
  opacity: 1;
  text-decoration: underline;
  cursor: pointer;
  font-style: italic;
  letter-spacing: 0.005rem;
}
.addToCartIcon {
  margin-left: 0.5rem;
  min-width: max-content;
}
.details {
  --bg-color: rgb(250, 250, 250);
  display: inline-flexbox;
  font-size: 0.82rem;
  background-color: var(--bg-color);
  border-radius: 0.4rem;
  padding: 1.5rem;
  margin: 0 2em;
  color: rgb(75, 75, 75);
  box-shadow: 0rem 0rem 0.4rem 0rem var(--bg-color);
}
.tagsCol,
.tempoCol,
.keyCol {
  min-width: fit-content;
}
.inCartTitle,
.inCartDescript {
  text-align: center;
  opacity: 1;
}
.inCartTitle {
  font-variation-settings: "wght" 800;
  padding-top: 1rem;
}
.inCartDescript {
  display: flex;
  font-size: 0.75rem;
}
