.AudioPlayer_progressBar__1gNLL {
  /* default, for chrome */
  box-shadow: var(--box-shadow);
  --bar-bg: rgb(225, 225, 225);
  --seek-before-width: 0%;
  --seek-before-color: black;
  --playhead: rgb(0, 112, 0);
