.banner_wrapper {
  position: sticky;
  margin: 0.8rem;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1500;
}
.banner {
  background-image: radial-gradient(rgb(12, 12, 12, 0.95) 60%, rgb(0, 0, 0, 1));
  /* background-color: rgb(12, 12, 12, 0.95); */
  border-radius: 1rem;
  padding: 1.5rem 4rem;
  display: flex;
  flex-direction: row;
  /* flex-flow: row wrap; */
  align-items: center;
  justify-content: space-around;
  box-shadow: 1rem 0.5rem 1.5rem rgb(50, 50, 50, 0.5),
    -1rem 0.5rem 1.5rem rgb(50, 50, 50, 0.5);
}
.item {
  padding: 0.25rem 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  color: rgb(250, 250, 250);
  cursor: pointer;
  font-size: 1.25rem;
  font-weight: 515;
  letter-spacing: 0.05rem;
}
.item:hover {
  letter-spacing: 0.06rem;
  color: white;
  font-weight: 500;
}
.logo {
  max-width: fit-content;
  padding: 0rem 4rem;
  font-family: "Adria Deco", -apple-system, BlinkMacSystemFont, "Segoe UI",
    Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  color: white;
  font-size: 2rem;
  font-weight: bolder;
  letter-spacing: 0.075rem;
  cursor: pointer;
}
.logo:hover,
.item:hover {
  --hover-base: 0.05rem 1rem rgba(255, 228, 196, 0.65);
  --hover-shadow-left: -1rem var(--hover-base);
  --hover-shadow-right: 1rem var(--hover-base);
  text-shadow: var(--hover-shadow-left), var(--hover-shadow-right);

  animation-name: hover-animation;
  animation-duration: 0.15s;
}
@font-face {
  font-family: "Adria Deco";
  src: url("./fonts/AdriaDeco-webfont.woff") format("woff"),
    url("./fonts/AdriaDeco-webfont.woff2") format("woff2");
}
@keyframes hover-animation {
  from {
    text-shadow: inherit;
  }
  to {
    text-shadow: var(--hover-shadow-left), var(--hover-shadow-right);
  }
}
