.popupWrapper {
  --padding-vertical: 1rem;
  --padding-horizontal: 0rem;
  position: fixed;
  z-index: 2000;
}
.windowWrap {
  display: flex;
  justify-content: center;
  align-items: center;
}
.window {
  display: flex;
  justify-content: center;
  width: 45%;
  height: 75%;
  background-color: white;
  box-shadow: 0.5rem 0.5rem 2rem 1rem rgba(0, 0, 0, 0.25),
    0 0.2rem 1.2rem 0.05rem rgba(0, 0, 0, 0.37);
  padding: 2rem;
  border-radius: 0.8rem;
}
.titleText {
  position: relative;
}
.title {
  display: flex;
  color: white;
  background-color: goldenrod;
  font-variation-settings: "wght" 700;
  font-style: italic;
  width: fit-content;
  padding: 0rem 1rem;
  border-radius: 1.75rem;
}
.x {
  display: flex;
  justify-content: flex-end;
}
.closePopup,
.x {
  font-size: 0.8rem;
  opacity: 0.6;
}

.closePopup:hover,
.x:hover {
  opacity: 1;
  text-decoration: underline;
  cursor: pointer;
}
.closePopup:hover {
  font-style: italic;
  letter-spacing: 0.01rem;
}
.content {
  display: flex;
  flex-flow: column wrap;
  justify-items: center;
  align-content: center;
  padding-top: var(--padding-vertical);
  padding-bottom: var(--padding-vertical);
  padding-left: var(--padding-horizontal);
  padding-right: var(--padding-horizontal);
}
.descript {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  padding-top: 1rem;
  width: fit-content;
}
