.componentWrapper {
  display: flex;
  flex-direction: column;
  padding: 0rem;
  margin: 0rem;
  align-items: center;
}

.component {
  --text-shadow: 0.25rem 0.25rem 1rem rgb(0, 0, 0, 0.15);
  --cover-shadow: 0rem 0.35rem 1.1rem rgb(0, 0, 0, 0.25);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-items: center;
  justify-content: center;
  padding: 2rem;
  margin: 0.7rem 0rem;
  background-color: whitesmoke;
  box-shadow: 0.1rem 1rem 2rem rgb(0, 0, 0, 0.08),
    0.1rem 1rem 2.6rem rgb(0, 0, 0, 0.07);
  border-radius: 1rem;
  width: fit-content;
  min-width: 60%;
}
.cover {
  box-shadow: var(--cover-shadow);
}
.contentWrapper {
  display: flex;
  flex-direction: column;
  max-width: fit-content;
  justify-items: center;
  justify-content: center;
  align-content: center;
  align-items: center;
}
.rmFromCartButton {
  margin: 0.5rem;
  border-radius: 100rem;
  width: fit-content;
  padding-left: 1rem;
  padding-right: 1rem;
}
.rmFromCartButton:hover {
  box-shadow: 0rem 0rem 0.15rem var(--danger-hover);
}
.title {
  --offset: 0.95rem;
  --offset-opp: -0.95rem;
  display: inline-flex;
  font-variation-settings: "wght" 700;
  font-style: italic;
  padding-top: 1rem;
  padding-bottom: 1rem;
  text-shadow: var(--text-shadow);
  width: 100%;
}
.titleText {
  background-color: rgb(255, 255, 0, 0.85);
  box-shadow: 0.2rem 0rem 0.5rem 0rem rgb(255, 255, 0, 1),
    -0.2rem 0rem 0.5rem 0rem rgb(255, 255, 0, 1);
  border-radius: 0.25rem;
  padding: 0rem var(--offset);
}
.selectElem {
  width: 100%;
}

.cartInteractElem,
.detailElem {
  display: inline-flex;
  align-items: center;
  justify-items: center;
  justify-content: space-evenly;
}

.cartInteractElem {
  padding: 1rem 0rem 1rem 0rem;
  width: 100%;
}

.currentPrice {
  margin-top: 0.5rem;
  font-family: monospace;
  font-weight: bolder;
  padding-left: 1rem;
  padding-right: 1rem;
}
